.content-main-container{
    display: flex;
    flex-direction: column;
    max-width: 100vw;
    height: auto;
    /* height: 100vh; */
    align-items: center;
    margin: 0;
    padding: 0 2%;
    border: 1px dashed green;
}
.main-heading{
    color: #330000;
    font-family: "Mandali", Sans-serif;
    font-size: 20px;
    font-weight: 400;
}
.content-main-container ul {
    margin: 0;
    padding: 2% 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.content-main-container ul li{
    list-style-type:none;
    display: flex;
    flex-direction:column;

}
.content-main-container ul li img{
    width:230px;
    /* height: 172px; */
}
.content-main-container ul li p{
   font-size: 12px;
}