.list-item-details-container{
    display: flex;
    padding: 0 10%;
}

.list-item-details-video{
    display: flex;
    flex-direction: column;
    width:70%;
    margin: 2% 0 0;
    padding: 0 2%;
    text-align: left;
}
.list-item-details-video h2{
    margin: 0;
    padding: 0;
    color: #810000;
    font-family: "Mandali", Sans-serif;
    font-size: 20px;
    font-weight: 400;
    font-style: normal;
}
.list-item-details-video p{
    color: #300;
    font-family: "Rubik", Sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
}
.list-item-details-video iframe{
    border: none;
}

.list-item-details-latest-news{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width:30%;
    margin: 2% 0 0;
    padding: 0;
    background-color: #fafafa;
    border-color: #ffe4e4;
}
.list-item-details-latest-news h2{
    margin:0;
    padding: 1% 0;
    font-size: 18px;
    font-weight: normal;
    color:#ffffff;
    background-color: #f33;
}
