.listItem-container{
    display: flex;
    margin: 0 0 0.5% 0;
    
}

.listItem-container li{
    list-style-type: none;
    margin: 0;
    padding: 0;
    max-width: 230px;
    background-color: #f7eeee;
    border-radius: 10px;
    /* border: 1px solid orange; */
}
.listItem-container li a{
    text-decoration: none;  
}
.listItem-container li p{
    color:#300;
    font-size: 12px;
    text-align: left;
    padding: 2% 2%;
    margin: 0;
}